<template>
  <transition name='tantan'>
    <div id="editor">
      <simplert useRadius=true
          useIcon=true
          ref="simplert"></simplert>
      <mavon-editor style="height: 100%" v-model="value"></mavon-editor>
      <button class="btn btn-success" @click='upload_event'>点击提交{{value}}</button>
      <button class="btn btn-danger" @click='stopedit'>结束编辑</button>
      <button class="btn btn-into" @click='notice'>测试alert</button>
    </div>
  </transition>
</template>

<script>
  // Local Registration
  import {
    mavonEditor
  } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  export default {
    name: 'editor',
    data () {
      return {
        value: '',
        event_location: {},
        event_title: ''
      }
    },
    mounted: function () {
      if (!this.$store.state.editing) {
        alert('尚未进入编辑状态，请通过点击骚东西来编辑')
        this.$router.push('/')
        this.$destroy() // 清除自身的一切数据
      }
      this.event_location = this.$route.params.location
    },
    computed: {
      ajax_data () {
        const type = this.$store.state.opt.params.type
        if (type === 'EVENT_UPLOAD') {
          return {
            title: this.title,
            event: this.value,
            happentime: '2012-12-03 03:23:13',
            positionx: this.event_location.lng,
            positiony: this.event_location.lat
          }
        }
      }
    },
    components: {
      mavonEditor
      // or 'mavon-editor': mavonEditor
    },
    methods: {
      upload_event () {
        if (this.location === {}) {
          alert('事件的地点还没选择')
          this.$router.push('/')
        } else {
          console.log(this.event_location)
        }
      },
      stopedit () {
        this.$store.commit('stopedit')
        this.$router.push('/')
        this.$destroy() // 清除自身的一切数据
      },
      notice () {
        var obj = {
          isShown: true,
          title: '提示',
          message: '测试一下',
          type: 'success',
          onClose: this.onClose
        }
        this.$refs.simplert.openSimplert(obj)
      }
    }
  }
</script>

<style>
  #editor {
    position: absolute;
    top: 100px;
    left: 60px;
    right: 60px;
    bottom: 100px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    z-index: 0
  }
  .tantan-enter-active {
    animation: tantan-in .5s ease;
  }
  .tantan-leave-active {
    animation: tantan-out .5s ease;
  }
  @keyframes tantan-in {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  @keyframes tantan-out {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(100%);
    }
  }
</style>



